.select {
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 0.375rem;
  transition: box-shadow 0.15s ease-in-out;
  outline: none;
  background-color: var(--background-color);
  color: var(--color);

  &:focus {
    box-shadow: 0 0 0 0.2rem rgba(13, 110, 253, 0.25);
  }

  &.error {
    border: 1px solid red;
  }
}
